.navbar {
    direction: rtl;
    margin-bottom: 0;
    background-color: rgba(255, 255, 255, 0.35);
    z-index: 9999;
    border: 0;
    font-size: 20px;
    line-height: 2;
    border-radius: 0;
    transition: all 0.3s;
    position: fixed;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}


.navbar:hover{
    background-color: rgba(255, 255, 255, 0.6);
}
a{
    text-decoration: none;
}
a:visited{
    color: black;
}

